.todo {
  padding-top: 70px;
  @media (max-width: 750px) {
    padding-top: 40px;
  }
  @media (max-width: 550px) {
    padding-top: 20px;
  }
  &__title {
  }

  ul {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 50px 50px;
    @media (max-width: 1050px) {
      gap: 25px 25px;
    }
    @media (max-width: 750px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 550px) {
      gap: 15px;
    }

    .item {
      max-height: 510px;
      height: 100%;
      z-index: 999;
      box-shadow:
        10px 10px 19px #1c1e22,
        -10px -10px 19px #262a2e;
      padding: 35px 55px 55px 55px;
      border-radius: 5px;
      background: linear-gradient(to right bottom, #212428, #16181c);
      @media (max-width: 1050px) {
        padding: 20px 20px 20px 20px;
      }
      @media (max-width: 550px) {
        padding: 10px;
      }
      :hover {
      }

      li {
        display: flex;
        gap: 20px 0;
        flex-direction: column;
        @media (max-width: 1050px) {
          gap: 30px 0;
        }
        @media (max-width: 750px) {
          gap: 15px 0;
        }
        img {
          margin: 0 auto;
          border-radius: 5px;
          max-width: 350px;
          width: 100%;
          max-height: 200px;
          height: 100%;
        }
        h4 {
          font-weight: 500;
          font-size: 24px;
          line-height: 32px;
          @media (max-width: 750px) {
            font-size: 20px;
            line-height: 22px;
          }
          @media (max-width: 500px) {
            font-size: 12px;
            line-height: 14px;
          }
        }

        p {
          font-size: 16px;
          line-height: 28px;
          @media (max-width: 750px) {
            line-height: 20px;
          }
          @media (max-width: 500px) {
            font-size: 10px;
            line-height: 14px;
          }
        }
      }
    }
  }
}
