.list {
  flex: 1 1 30%;
  padding: 30px;
  border-radius: 10px;
  background-color: #212428;
  display: flex;
  flex-direction: column;
  justify-content: start;
  gap: 10px 0;
  color: #abb8c3;
  box-shadow:
    10px 10px 19px #1c1e22,
    -10px -10px 19px #262a2e;
  @media (max-width: 850px) {
    flex-direction: row;
    gap: 0 30px;
    padding: 16px;
  }

  li {
    span {
      cursor: pointer;
      color: #e4e6ea;

      &:hover {
        color: #f9004d;
      }
    }
  }
}
.info {
}
.listImg {
  margin-bottom: 28px;
  overflow: hidden;
  border-radius: 10px;

  img {
    width: 100%;
    @media (max-width: 850px) {
      border-radius: 10px;
    }
  }
  @media (max-width: 450px) {
    display: none;
  }
}

.title {
  font-size: 29px;
  line-height: 44px;
  color: #e4e6ea;
  margin-bottom: 9px;
  font-weight: 600;
  @media (max-width: 600px) {
    font-size: 18px;
    line-height: 20px;
  }
}

.desc {
  font-size: 18px;
  margin-top: 10px;
  margin-bottom: 10px;
  @media (max-width: 600px) {
    font-size: 16px;
    line-height: 20px;
  }
}

.email {
  margin-top: 10px;
  margin-bottom: 25px;
}

.social {
  display: flex;
  gap: 0 20px;
  margin-top: 20px;
  @media (max-width: 500px) {
    gap: 0 10px;
  }
  &__item {
    background: #16181c;
    display: block;
    border-radius: 5px;
    padding: 7px;
    box-shadow:
      10px 10px 19px #1c1e22,
      -10px -10px 19px #262a2e;

    img {
      width: 35px;
      height: 35px;
      transition: 0.2s;
      @media (max-width: 600px) {
        width: 25px;
        height: 25px;
      }

      &:hover {
        transform: scale(1.2);
      }
    }
  }
}
